.performance{
    background: #282D30;    
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.0212249);
    grid-area: 3 / 2 / 5 / 3;     
}

.performance_chart{
    height: 100%;
    width: 100%;    
}

.performance .recharts-text{
    fill: white;
    font-size: 1.2rem;
    font-weight: 500;
}

.performance .recharts-text:first-letter{
    text-transform: capitalize;
}

.recharts-polar-grid-angle{
    display: none;
}